import Vue from 'vue'
import Router from 'vue-router'
import Hello from '@/components/Hello'
import Shop from '@/components/Shop/Index'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/hello',
      name: 'Hello',
      component: Hello
    },
    {
      path: '/',
      name: 'Shop',
      component: Shop
    }
  ]
})



