<template>
  <div class="content">
    <mt-navbar v-model="selected">
      <mt-tab-item  v-for="item in types" :key="item.id" :id="item.id">{{item.name}}</mt-tab-item>
    </mt-navbar>
    <!-- tab-container -->
    <mt-tab-container v-model="selected">
      <mt-tab-container-item v-for="item in newsList" :id="item.type" :key="item.type">
        <mt-cell v-for="news in item.data" :key="news.title" :title="news.title" :href="news.url"
                 is-link :label="'来源：'+ news.author_name">
          <span>详情</span>
          <img slot="icon" :src="news.thumbnail_pic_s" width="40%">
        </mt-cell>
      </mt-tab-container-item>
    </mt-tab-container>
  </div>
</template>
<script>
  export default {
    name: 'list',
    mounted: function () {
      this.getNewsTypeList()
      this.getNewsList()
    },
    data: function () {
      return {
        selected: '',
        types: [],
        newsList: []
      }
    },
    methods: {
      getNewsList: function () {
        this.$http.post('/news/getData').then((response) => {
          this.newsList = response.data
          console.log(response.data)
          console.log(this.newsList)
        })
      },
      getNewsTypeList: function () {
        this.$http.post('/news/getNewsTypeList').then((response) => {
          this.types = response.data
          if (this.types.length > 0) {
            this.selected = this.types[0].id
          }
          console.log(this.types)
        })
      }
    }
  }
</script>

<style scope>
  .shop-bannar{
    height: 140px;
  }
  .mint-tab-container-wrap {
    margin-top: 5px;
  }
  .mint-cell-text {
    display: -webkit-box;
    font-size: 13px;
    text-align: -webkit-left;
    text-indent: 5px;
    margin: 15px;
    line-height: 19px;
    max-height: 50px;
    overflow: hidden;
  }
  .mint-cell img {
    margin: 5px auto;
    margin-right: 5px;
    float: left;
  }
  .mint-cell-label {
    display: -webkit-box;
  }
  .mint-tab-item-label {
    font-size: 15px;
  }
  .mint-cell-value {
    font-size: 13px;
  }
</style>



